<template>
    <el-container v-if="type=== 'iFun'">
        <el-form :model="form" :rules="rules" ref="ruleForm" label-width="100px" style="width: 100%">

            <el-row>
                <el-col :span="12">
                    <el-form-item label="" prop="zhTitle">
                        <el-input placeholder="" v-model="form.zhTitle"/>
                    </el-form-item>
                    <el-form-item label="" prop="enTitle">
                        <el-input placeholder="" v-model="form.enTitle"/>
                    </el-form-item>


                    <el-form-item label="" prop="abstract">
                        <el-input
                                type="textarea"
                                autosize
                                placeholder=""
                                v-model="form.abstract">
                        </el-input>
                    </el-form-item>
                    <el-form-item label="" prop="label">
                        <el-select value=""
                                   v-model="form.label"
                                   multiple
                                   filterable
                                   allow-create
                                   default-first-option
                                   placeholder="">
                            <el-option
                                    v-for="item in labelList"
                                    :key="item._id"
                                    :label="item.label"
                                    :value="item.label">
                            </el-option>
                        </el-select>

                    </el-form-item>
                    <el-form-item label="" prop="zhVvt">
                        <el-input placeholder="" v-model="form.zhVvt"/>
                    </el-form-item>

                    <el-form-item label="" prop="enVvt">
                        <el-input placeholder="" v-model="form.enVvt"/>
                    </el-form-item>
                </el-col>
                <el-col :span="6" :offset="2">
                    <el-form-item label="" prop="class">
                        <el-select placeholder="" v-model="form.class" value="">
                            <el-option
                                    v-for="(index,value) in iFunTypeList"
                                    :key="value"
                                    :label="index"
                                    :value="value">
                            </el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="" prop="difficulty">
                        <el-select placeholder="" v-model="form.difficulty" value="">
                            <el-option label="" value="simple"/>
                            <el-option label="" value="easily"/>
                            <el-option label="" value="difficulty"/>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>
    </el-container>
</template>

<script>
    export default {
        props: {
            type: String,
            form: {
                type: Object,
                default() {
                    return {}
                }
            },
            labelList: Array,
        },
        data() {
            return {
                iFunTypeList: {
                    funny: '',
                    movie: '',
                    entertainment: '',
                    culture: '',
                    travel: '',
                    arts: '',
                    foods: '',
                    sports: '',
                    technology: '',
                },
                rules: {
                    zhTitle: [
                        {required: true, message: '', trigger: 'blur'},
                    ],
                    enTitle: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    abstract: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    zhVvt: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    enVvt: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    class: [
                        {required: true, message: '', trigger: 'change'}
                    ],
                    difficulty: [
                        {required: true, message: '', trigger: 'change'}
                    ]
                }
            }
        },
        mounted() {
        },
        methods: {
            submitForm(formName) {
                return new Promise((resolve, reject) => {
                    this.$refs[formName].validate((valid) => {
                        if (valid) {
                            resolve(true)
                        } else {
                            this.$message.warning('');
                            reject(false);
                        }
                    });
                })
            },
        }
    }
</script>

<style scoped>
</style>