<template>
    <el-row style="padding: 25px 0">
        <el-col>

            <el-input v-model="filterList.title" style="width: 25%" size="small"
                      placeholder="" suffix-icon="el-icon-search"/>

            <el-select
                    v-show="listType === 'media'"
                    default-first-option
                    value=""
                    v-model="filterList.category"
                    size="small"
                    placeholder="iFun iTalk..">
                <el-option label="iFun" value="fun"/>
                <el-option label="iTalk" value="talk"/>
                <el-option label="iListen" value="listen"/>
                <el-option label="iWords" value="word"/>
            </el-select>

            <el-select placeholder="" v-show="listType === 'fun'" v-model="filterList.class" value="" size="small">
                <el-option
                        v-for="(index,value) in iFunTypeList"
                        :key="value"
                        :label="index"
                        :value="value">
                </el-option>
            </el-select>

            <el-select placeholder="" v-show="listType === 'word'" v-model="filterList.class" value="" size="small">
                <el-option
                        v-for="(index,value) in iWordTypeList"
                        :key="value"
                        :label="index"
                        :value="value">
                </el-option>
            </el-select>

            <el-select placeholder="" v-show="listType === 'media'"
                       v-model="filterList.source" value="" size="small">
            </el-select>

            <el-select placeholder="" v-model="filterList.difficulty" value="" size="small">
                <el-option label="" value="all"/>
                <el-option label="" value="1"/>
                <el-option label="" value="2"/>
                <el-option label="" value="3"/>
                <el-option label="" value="4" v-if="listType === 'talk'"/>
            </el-select>

            <el-select
                    multiple
                    filterable
                    allow-create
                    default-first-option
                    value=""
                    v-model="filterList.label"
                    size="small"
                    placeholder="">
                <el-option
                        v-for="item in labelList"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value"></el-option>
            </el-select>

            <el-select placeholder="" v-show="listType !== 'media'" v-model="filterList.status" value="" size="small">
                <el-option label="" value="all"/>
                <el-option label="" value="1"/>
                <el-option label="" value="2"/>
                <el-option label="" value="3"/>
            </el-select>

            <el-button plain size="mini" @click="filterMediaList"></el-button>

            <el-button plain size="mini" @click="clearAll"></el-button>
        </el-col>
    </el-row>
</template>

<script>
    export default {
        name: "filter-list-item",
        props: {
            listType: String,
            filterList: Object,
            labelList: Array
        },
        data() {
            return {
                iFunTypeList: {
                    funny: '',
                    movie: '',
                    entertainment: '',
                    culture: '',
                    travel: '',
                    arts: '',
                    foods: '',
                    sports: '',
                    technology: '',
                },
                iWordTypeList: {
                    lifeStyle: '',
                    oversea: '',
                    date: '',
                    tourGuide: '',
                    serious: '',
                    series: '',
                    dailyWord: ''
                },
            }
        },
        mounted() {
            // this.getAllLabelData();
        },
        methods: {
            // getAllLabelData() {
            //     this.$axios.get('/label/_find').then((res) => {
            //         console.log(res.data.data)
            //         if (!res.data.code) {
            //             this.labelList = res.data.data
            //         }
            //     })
            // },
            clearAll() {
                Object.keys(this.filterList).forEach((value) => {
                    this.filterList[value] = '';
                })
            },
            filterMediaList() {
                this.$emit('filterMediaList', this.filterList);
            },
        }
    }
</script>

<style scoped>
    .el-select {
        width: 150px;
        margin: 0 5px;
    }
</style>